.header {
  margin-bottom: 1.5em;
}

.header__upper ~ .container {
  padding: 0;
}

.header__logo {
  display: block;

  width: 100%;
  margin-bottom: 1em;

  .header__svg {
    width: 100%;
  }
}

.header__search {
  margin-bottom: 1em;
}

.header__input {
  width: 100%;
  padding: 0.5em 2em 0.5em 0.5em;

  border: none;
  border-bottom: 1px solid $grey;
  background: url(../img/icons/icon-search.svg) 100% center no-repeat;

  &::placeholder {
    color: $grey;
  }

  &:hover,
  &:focus {
    border-bottom: 1px solid $green;
  }
}

.header__contacts {
  width: 100%;
  padding-bottom: 2em;
}

.header__contacts-container {
  display: flex;

  justify-content: space-between;
}

.header__tel {
  display: flex;

  width: 50%;

  text-decoration: none;

  font-family: $prata;
  font-size: 0.9em;
  line-height: 1em;

  color: $black;

  align-items: center;
}

.header__socials {
  display: flex;

  width: 47%;

  align-items: center;
  justify-content: space-between;
}

.header__lower {
  border-top: 1px solid $grey;
  border-bottom: 1px solid $grey;
}

.header__list {
  display: flex;

  padding: 1em 0;

  flex-wrap: wrap;
  justify-content: space-around;
}

.header__item {
  margin-bottom: 1em;
  padding: 0 0.5em;
}

.header__link {
  transition: all 0.35s ease-in;
  text-decoration: none;

  font-size: 0.9em;
  font-weight: 500;

  color: $grey;

  &:hover,
  &:focus {
    color: #797979;
  }

  &:active {
    color: $black;
  }
}

@include media-tablet-desktop {
  .header {
    margin-bottom: 0.5em;
  }

  .header__upper .container {
    display: flex;

    justify-content: space-between;
  }

  .header__container {
    display: flex;

    padding: 0;

    flex-grow: 1;
    align-items: center;
  }

  .header__logo {
    width: auto;
    margin-bottom: 0;
  }

  .header__contacts {
    display: flex;

    max-width: 172px;
    padding-bottom: 0;

    align-items: center;
  }

  .header__contacts-container {
    display: flex;

    padding: 0;

    flex-wrap: wrap;
  }

  .header__tel {
    width: 100%;
    margin-bottom: 0.5em;
  }

  .header__socials {
    width: 100%;
  }

  .header__social {
    transition: all 0.35s ease-in;

    &:hover,
    &:focus {
      filter: brightness(0.4);
    }
  }

  .header__search {
    display: flex;

    max-width: 400px;
    margin-bottom: 0;
    padding: 0 1em;

    flex-grow: 1;
    align-items: center;
  }

  .header__item {
    margin-bottom: 0;
  }
}
