*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;

  font-family: $montserrat;
}

body {
  margin: 0;

  font-size: 20px;
  line-height: 1.2em;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  margin-bottom: 5em;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;

  overflow: hidden;
  clip: rect(0, 0, 0, 0);

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  white-space: nowrap;

  border: 0;

  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1110px;
  margin: 0 auto;
  padding: 0 20px;

  @include media-desktop {
    padding: 0 30px;
  }
}

.heading {
  margin-bottom: 60px;

  text-align: center;

  font-family: $prata;
  font-size: 30px;
  font-weight: 400;
  line-height: 1.35em;

  @include media-desktop {
    font-size: 36px;
  }
}

.accordeon__content {
  overflow: hidden;

  max-height: 0;

  transition: all 0.8s ease-out;

  opacity: 0;
}

.accordeon__content--opened {
  max-height: 100%;
  padding: 20px;

  opacity: 1;
}

.button {
  width: fit-content;
  padding: 0.9em 2.5em;

  cursor: pointer;
  transition: opacity 0.35s;
  text-decoration: none;

  font-family: $prata;
  font-size: 16px;

  color: $white;
  border: none;
  border-radius: 10px;
  background: $gradient;

  &:hover,
  &:focus {
    opacity: 0.8;
    background: $green;
  }
}

.accordeon__button::before {
  position: absolute;
  right: 0;

  content: '+';
  -webkit-transform: translate(-50%, 10%);
  transform: translate(-50%, 10%);

  font-size: 50px;
  font-weight: 500;

  color: #397719;
}

.accordeon__button--opened::before {
  content: '-';
  transform: translate(-70%, -25%);

  font-size: 70px;
}

.swiper-button-next {
  &:hover svg,
  &:focus svg {
    filter: brightness(35%);
  }
}

.swiper-button-prev {
  transition: all 0.35s;
  transform: rotate(180deg);

  &:hover svg,
  &:focus svg {
    filter: brightness(35%);
  }
}

.swiper-wrapper {
  display: flex;

  align-items: center;
}

