.item {
  overflow: hidden;

  margin-bottom: 1.5em;

  border-radius: 7px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);

  &:hover .item__overlay,
  &:focus .item__overlay {
    opacity: 0.1;
  }
}

.item__link {
  text-decoration: none;
}

.item__up {
  position: relative;
}

.item__bottom {
  display: flex;

  padding: 1em;

  flex-wrap: wrap;
  justify-content: space-around;
}

.item__overlay {
  position: absolute;
  top: 0;

  width: 100%;
  height: 100%;

  transition: all 0.35s ease-in;

  opacity: 0.4;
  background: #000000;
}

.item__heading {
  position: absolute;
  z-index: 2;
  bottom: 0;

  margin-bottom: 1em;
  margin-left: 1em;

  font-size: 0.8em;
  font-weight: 600;

  color: $white;
}

.item__img {
  width: 100%;
  height: 280px;

  object-fit: cover;
}

.item__price {
  display: flex;

  width: 100%;
  margin-bottom: 0.5em;

  font-size: 0.9em;
  font-weight: 600;
  line-height: 2.3em;

  color: $light-green;

  justify-content: center;
  align-items: center;
}

.item__more {
  display: flex;

  margin-right: 0.5em;
  margin-bottom: 0.5em;

  transition: all 0.35s ease-in;
  text-decoration: underline;

  font-size: 0.9em;

  color: $grey;

  align-items: center;

  &:hover {
    color: $black;
  }
}

.item__button {
  margin-bottom: 0.5em;

  font-size: 0.8em;
}

.item__icons {
  display: flex;
}

.item__icon {
  margin-right: 0.5em;
}

@include media-tablet-desktop {
  .item {
    box-shadow: none;
  }

  .item__heading {
    font-size: 1.1em;
    line-height: 1.25em;
  }

  .item__bottom {
    justify-content: space-between;
  }

  .item__price {
    width: auto;
    margin: 0;
  }

  .item__more {
    margin: 0;

    order: 1;
  }

  .item__button {
    margin: 0;

    order: 0;
  }

  .item__button {
    margin: 0;
  }
}
