.do__card {
  position: relative;

  overflow: hidden;

  min-height: 539px;
  margin-bottom: 2em;
  padding: 2em 1em;

  border-radius: 10px;
  background-size: cover;

  &--stairs {
    background-image: url(../img/about-1.jpg);
    background-repeat: no-repeat;
    background-position: 0 0;
  }

  &--interier {
    background-image: url(../img/about-2.jpg);
    background-repeat: no-repeat;
    background-position: 0 0;
  }

  &--furniture {
    background-image: url(../img/about-3.jpg);
    background-repeat: no-repeat;
    background-position: 0 0;
  }

  &:hover .do__card-overlay {
    opacity: 0.65;
  }
}

.do__card-overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  transition: all 0.35s ease-in;

  opacity: 0.7;
  background: #000000;
}

.do__sub-heading {
  position: relative;
  left: -30px;

  margin-bottom: 1.3em;
  padding: 0.5em 1em 0.5em 1.5em;

  font-family: $prata;
  font-size: 1.5em;

  color: $green;
  border-radius: 10px;
  background-color: $white;
}

.do__item {
  position: relative;

  display: block;

  margin-bottom: 1em;
  padding-left: 1em;

  transition: all 0.35s ease-in;
  text-decoration: none;

  font-weight: 600;

  color: $white;

  &:last-of-type {
    margin-bottom: 0;
  }

  &::before {
    position: absolute;
    left: 0;

    content: '+';
    transform: rotate(45deg);

    font-size: 1em;
    font-weight: 600;

    color: #75b355;
  }

  &:hover,
  &:focus {
    color: $light-green;
  }

  &:active {
    color: $green;
  }
}

@include media-tablet-desktop {
  .do__card {
    min-height: 100%;
    // background-size: contain;
  }

  .do__sub-heading {
    width: fit-content;
    padding: 0.6em 1.2em;

    font-size: 1.8em;

    border-radius: 5px;
  }

  .do__list {
    display: flex;

    flex-wrap: wrap;
    // justify-content: space-between;
  }

  .do__item {
    width: 32%;
    padding-right: 0.5em;

    font-size: 1.1em;
  }
}
