.form__input {
  width: 100%;
  margin-bottom: 1em;
  padding: 1em;

  transition: all 0.35s;

  border: 1px solid $grey;
  border-radius: 10px;

  &:last-of-type {
    margin-bottom: 2em;
  }

  &::placeholder {
    color: $grey;
  }

  &:hover,
  &:focus {
    border: solid 1px $green;
  }
}

.form__button {
  width: 100%;
  margin-bottom: 1.2em;
  padding: 1.2em;
}

.form__button-info {
  font-size: 0.7em;

  a {
    color: $green;
  }
}

@include media-tablet-desktop {
  .form {
    display: flex;

    align-items: flex-start;
    justify-content: space-between;
  }

  .form__input {
    width: 33%;
    max-width: 320px;
    margin-bottom: 0;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  .form__button {
    max-width: 320px;
    margin-bottom: 0.5em;
    padding: 1.45em;
  }

  .form__button-info {
    font-size: 0.8em;
    line-height: 1.2em;
  }

  .form__button-container {
    display: flex;

    width: 33%;

    flex-wrap: wrap;
  }
}
