.stages {
  background-color: #f5f5f5;
}

.stages__heading {
  padding-top: 3em;
}

.stages__item {
  position: relative;

  margin-bottom: 4em;
  padding: 3em 1em 1em 1em;

  text-align: center;

  font-size: 0.8em;
  line-height: 1.8em;

  border-radius: 7px;
  background-color: $white;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
}

.stages__img {
  position: absolute;
  top: -2em;
  left: 50%;

  transform: translate(-50%, 0);
}

@include media-tablet-desktop {
  .stages__list {
    display: flex;

    flex-wrap: wrap;
    justify-content: space-between;
  }

  .stages__item {
    width: 30%;
    height: fit-content;
    margin-right: 1.5em;

    &:nth-of-type(3n) {
      margin-right: 0;
    }
  }
}
