.card .container {
  max-width: 600px;
  margin: 0 auto;
}

.card__heading {
  padding-top: 1.1em;
  padding-bottom: 1.1em;

  color: $light-green;
}

.card__swiper {
  display: flex;

  margin-bottom: 1em;

  flex-wrap: wrap;
  align-items: stretch;
}

.card__previews {
  width: 100%;
}

.card__slider {
  width: 100%;
}

.card__preview {
  position: relative;

  display: flex;

  cursor: pointer;

  justify-content: center;

  &:hover .card__preview-overlay,
  &:focus .card__preview-overlay {
    opacity: 0;
  }

  &.swiper-slide-thumb-active .card__preview-overlay {
    opacity: 0;
  }
}

.card__preview-overlay {
  position: absolute;
  top: 0;

  width: 100%;
  height: 100%;

  transition: all 0.35s ease-in;

  opacity: 0.6;
  background: #000000;
}

.card__heading {
  width: 100%;
}

.card__slider {
  margin-bottom: 0.5em;
}

.card__slide {
  display: flex;

  background-color: rgba(0, 0, 0, 0.1);

  align-items: center;
  justify-content: center;
  aspect-ratio: 1/1;
}

@media(min-width:1070px) {
  .card .container {
    display: flex;

    max-width: 1110px;
  }

  .card__swiper {
    width: 600px;
    margin-right: 1em;
    margin-bottom: 0;
  }

  .card__description {
    display: flex;

    flex-grow: 1;
  }

  .card__preview {
    min-width: 193px;
  }
}
