.main-slider .container {
  position: relative;

  width: 100%;
}

.main-slider__img {
  width: 100%;
}

.main-slider__info {
  position: absolute;
  left: 0;

  display: flex;

  width: 100%;
  height: 100%;

  font-family: $prata;
  font-size: 1.5em;
  line-height: 1.35em;

  color: $white;

  align-items: flex-end;
  justify-content: center;

  span {
    width: 70%;
    margin-bottom: 2em;
  }
}

@media(min-width: 460px) {
  .main-slider__info {
    justify-content: flex-start;
    align-items: center;

    span {
      width: 46%;
      margin-bottom: 0;
      margin-left: 1.5em;

      font-size: 0.65em;
    }
  }
}

@media(min-width: 560px) {
  .main-slider__info {
    span {
      width: 44%;
      margin-left: 1.6em;

      font-size: 1em;
    }
  }
}

@include media-desktop {
  .main-slider__info {
    justify-content: flex-start;
    align-items: center;

    span {
      max-width: 380px;
      margin-left: 2em;

      font-size: 1.2em;
      line-height: 1.35em;
    }
  }
}
