.footer {
  background-color: #000000;
}

.footer__logo {
  display: flex;

  padding-top: 2em;
  padding-bottom: 1em;

  justify-content: center;
}

.footer__svg {
  fill: $white;
}

.footer__contacts {
  display: flex;

  margin-bottom: 2em;
}

.footer__tel {
  display: flex;

  width: 50%;

  text-decoration: none;

  font-family: $prata;
  font-size: 0.9em;
  line-height: 1em;

  color: $white;

  align-items: center;
}

.footer__socials {
  display: flex;

  width: 47%;

  align-items: center;
  justify-content: space-between;
}

.footer__social {
  &:hover,
  &:focus {
    filter: contrast(500%);
  }
}

.footer__list {
  display: flex;

  padding: 1em 0;

  flex-wrap: wrap;
  justify-content: space-around;
}

.footer__item {
  margin-bottom: 1em;
  padding: 0 0.5em;
}

.footer__link {
  transition: all 0.35s ease-in;
  text-decoration: none;

  font-size: 0.9em;
  font-weight: 500;

  color: $white;

  &:hover,
  &:focus {
    color: $light-green;
  }

  &:active {
    color: $green;
  }
}

.footer {
  padding-bottom: 2em;

  .do__card {
    overflow: initial;

    min-height: 100%;
    padding: 0;
  }

  .do__sub-heading {
    background-color: transparent;
  }

  .do__item {
    text-decoration: underline 1px;

    font-weight: 500;
  }
}

@media(min-width: 860px) {
  .footer {
    .do__list {
      display: block;
    }

    .do__cards {
      display: flex;

      justify-content: space-between;
    }

    .do__card {
      width: 33%;
    }

    .do__sub-heading {
      left: 0;

      padding: 0;
    }

    .do__item {
      width: auto;
    }
  }

  .footer__upper {
    display: flex;

    padding: 40px 0 60px 0;
  }

  .footer__logo {
    padding: 0;
  }

  .footer__contacts {
    display: flex;

    min-width: 172px;
    margin-bottom: 0;

    order: 1;
    flex-wrap: wrap;
  }

  .footer__tel {
    width: 100%;
  }

  .footer__socials {
    width: 100%;
  }

  .footer__nav {
    order: 0;
  }

  .footer__list {
    padding: 0;
  }
}
