.description {
  margin-bottom: 2em;
}

.description__item {
  margin-bottom: 1em;

  font-size: 0.95em;

  &:last-of-type {
    margin-bottom: 0;
  }

  &--price {
    font-weight: 600;

    color: $light-green;
  }
}

.description__name {
  font-weight: 600;
}

@media(min-width:1070px) {
  .description {
    display: flex;

    margin-bottom: 0;

    flex-wrap: wrap;
    align-items: stretch;
  }

  .description__item {
    width: 100%;
  }
}
