.pay__stage {
  margin-bottom: 1em;
  padding: 1em;

  font-size: 0.9em;
  font-weight: 600;

  color: $light-green;
  border-top: 1px $grey solid;
  border-bottom: 1px $grey solid;

  &::before {
    transition: all 0.35s;
  }

  &:last-of-type {
    margin-bottom: 0;
  }

  &:hover,
  &:focus {
    cursor: pointer;
  }

  &:hover::before,
  &:focus::before {
    opacity: 0.7;
  }
}

.pay__item {
  position: relative;
}
