.about__list {
  padding: 0 1.5em;
}

.about__item {
  position: relative;

  margin-bottom: 1.5em;
  padding-top: 5em;

  text-align: center;

  font-style: italic;

  &::before {
    position: absolute;
    top: 0;

    width: 80px;
    height: 80px;

    content: '';
    transform: translate(-50%, 0);

    background: url(../img/icons/icon-tick.svg) 0 0 no-repeat;
  }
}

.about__sub-heading {
  font-size: 1em;
}

@include media-tablet-desktop {
  .about__list {
    display: flex;

    padding: 0;

    justify-content: space-around;
  }

  .about__item {
    max-width: 290px;
    margin-bottom: 0;
    padding: 5em 0.5em 0 0.5em;
  }
}
