.design__slider-container {
  width: 100%;
}

.design__slide {
  position: relative;

  display: flex;

  background-color: rgba(0, 0, 0, 0.1);

  justify-content: center;
}

.design__img {
  height: 500px;

  object-fit: cover;
  object-position: 20%;
}

.design__button {
  position: absolute;
  bottom: 0;
  left: 50%;

  display: block;

  width: fit-content;
  margin-bottom: 2em;

  transform: translate(-50%, 0);
}

.design__info {
  position: relative;

  width: 100%;
  margin-bottom: 2em;
  padding: 1em 0;

  text-align: center;

  color: $grey;
  border: solid 1px $grey;
  border-radius: 10px;
}

.design__info-slider {
  width: 80%;
}
