.catalog__show-more {
  display: block;

  margin: 0 auto;

  cursor: pointer;
  transition: all 0.5s ease-in;

  font-weight: 600;

  color: $black;
  border: none;
  background: transparent;

  &:hover,
  &:focus {
    color: $green;
  }
}

@include media-tablet-desktop {
  .catalog__list {
    display: flex;

    flex-wrap: wrap;
    justify-content: center;
  }

  .catalog__item {
    width: 47.9%;
    margin-right: 1.5em;

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }
}
